<script>
  let FAQ = false;

  var openFAQ = () => {
    FAQ = !FAQ; // True
    console.log(FAQ);
    if (FAQ) {
      document.getElementById("faq").style.display = "flex";
    } else {
      document.getElementById("faq").style.display = "none";
    }
  };

  var closeFAQ = () => {
    FAQ = !FAQ;
    console.log(FAQ);
    if (!FAQ) {
      document.getElementById("scroll").style.display = "flex";
      document.getElementById("faq").style.display = "none";
    } else {
      null;
    }
  };

  let integration = {
    Discord: "https://discord.com/users/901404605336916018"
  };

  function scrollToReviewInCaseUSERIsBlindToSeeThereIsAReviewOnBottom() {
    var element = document.getElementById("Review");
    element.scrollTop += 10;
  }
</script>
