		<a class="Discord text-white text-xs lg:mx-10 md:mx-5 sm:mx-3 xs:mx-3 font-bold" href={integration.Discord} style="font-family:Montserrat;margin:0px 5px;">Discord</a>
		<div id="Review" class="text-white text-xs lg:mx-10 md:mx-5 sm:mx-3 xs:mx-3 font-bold" on:click={scrollToReviewInCaseUSERIsBlindToSeeThereIsAReviewOnBottom} style="font-family:Montserrat">Review</div>
